import React from "react";
import styles from "./CharacterList.module.css";
import {
  Card,
  Col,
  Divider,
  Form,
  Input,
  InputNumber,
  Modal,
  Row,
  Space,
  Typography,
} from "antd";
import Button from "antd-button-color";
import { UserAddOutlined, DeleteOutlined } from "@ant-design/icons";
import store from "../../store";

import FormItem from "antd/lib/form/FormItem";

const CharacterList: React.FC = () => {
  return (
    <React.Fragment>
      <Card title="Выберите персонажа" className={styles.main}>
        <ShowCharacters />
      </Card>
    </React.Fragment>
  );
};

interface Character {
  id: number;
  name: string;
  money: number;
}
interface AddCharacterI {
  character: Character;
}

const ShowCharacters: React.FC = () => {
  const [isModalVisible, setisModalVisible] = React.useState<boolean>(false);
  const initialValues = {
    name: "",
    surname: "",
    age: 18,
  };
  const onFinish = (values: any) => {
    alert(JSON.stringify(values));
  };

  interface ShowCharactersI {
    char1: Character;
    char2: Character;
    char3: Character;
  }
  let characters: ShowCharactersI = {
    char1: {
      id: store.getState().userSlice.char1.id,
      name: store.getState().userSlice.char1.name,
      money: store.getState().userSlice.char1.money,
    },
    char2: {
      id: store.getState().userSlice.char2.id,
      name: store.getState().userSlice.char2.name,
      money: store.getState().userSlice.char2.money,
    },
    char3: {
      id: store.getState().userSlice.char3.id,
      name: store.getState().userSlice.char3.name,
      money: store.getState().userSlice.char3.money,
    },
  };
  return (
    <React.Fragment>
      <Row gutter={[8, 0]}>
        {Object.entries(characters).map(([, character], i) =>
          character.id > 0 ? (
            <Col key={i} span={8}>
              <AddCharacter character={character} />
            </Col>
          ) : (
            <Col key={i} span={8}>
              <Card
                title="Пустой слот"
                bodyStyle={{
                  background: "linear-gradient(to top, #1a1a1a, #141414)",
                }}
              >
                <UserAddOutlined
                  style={{ fontSize: "5rem" }}
                  onClick={() => setisModalVisible(true)}
                />

                <Modal
                  visible={isModalVisible}
                  title="Создание персонажа"
                  closable={false}
                  destroyOnClose={true}
                  footer={[
                    <Button
                      form="createCharacter"
                      key="submit"
                      htmlType="submit"
                      type="primary"
                    >
                      Далее
                    </Button>,
                    <Button
                      key="reset"
                      htmlType="reset"
                      onClick={() => setisModalVisible(false)}
                    >
                      Отмена
                    </Button>,
                  ]}
                >
                  <Form
                    id="createCharacter"
                    initialValues={initialValues}
                    onFinish={onFinish}
                    preserve={false}
                  >
                    <Form.Item
                      name="name"
                      rules={[
                        {
                          type: "string",
                          required: true,
                          min: 3,
                          max: 24,
                        },
                        {
                          pattern: new RegExp(/^[a-zA-Z]+$/),
                          message:
                            "Поле может состоять только из букв латинского алфавита.",
                        },
                      ]}
                    >
                      <Input size="large" placeholder="Имя" />
                    </Form.Item>
                    <Form.Item
                      name="surname"
                      rules={[
                        {
                          type: "string",
                          required: true,
                          min: 2,
                          max: 24,
                        },
                        {
                          pattern: new RegExp(/^[a-zA-Z]+$/),
                          message:
                            "Поле может состоять только из букв латинского алфавита.",
                        },
                      ]}
                    >
                      <Input size="large" placeholder="Фамилия" />
                    </Form.Item>
                    <Form.Item
                      name="age"
                      rules={[
                        {
                          type: "number",
                          required: true,
                          min: 16,
                          max: 80,
                        },
                      ]}
                    >
                      <InputNumber
                        min={16}
                        max={80}
                        size="large"
                        placeholder="Возраст"
                        style={{ width: "100%" }}
                      />
                    </Form.Item>
                  </Form>
                </Modal>
              </Card>
            </Col>
          )
        )}
        <Divider />
        <Row>
          <Bottom />
        </Row>
      </Row>
    </React.Fragment>
  );
};

const AddCharacter: React.FC<AddCharacterI> = (props) => {
  const [isModalVisible, setisModalVisible] = React.useState<boolean>(false);
  const [dialogValue, setDialogValue] = React.useState<string>("");
  const handleDialogChange = () => {
    if (dialogValue === "удалить") alert(dialogValue);
    else alert("WRONG");
  };
  const char: Character = props.character;
  let formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  });
  return (
    <React.Fragment>
      <Card
        title={char.name}
        bodyStyle={{
          textAlign: "left",
          background: "linear-gradient(to top, #1a1a1a, #141414)",
        }}
      >
        <Space direction="vertical">
          <Typography.Text>#: {char.id}</Typography.Text>
          <Typography.Text>
            Наличные: {formatter.format(char.money)}
          </Typography.Text>
        </Space>
        <Divider />
        <Row justify="center" gutter={[4, 0]}>
          <Col>
            <Button type="primary" shape="round">
              Выбрать
            </Button>
          </Col>
          <Col>
            <Button
              type="danger"
              shape="round"
              icon={<DeleteOutlined />}
              onClick={() => setisModalVisible(true)}
            />
            <Modal
              title="Подтвердите действие"
              visible={isModalVisible}
              closable={false}
              footer={[
                <Button key="submit" type="danger" onClick={handleDialogChange}>
                  Удалить
                </Button>,
                <Button key="back" onClick={() => setisModalVisible(false)}>
                  Отмена
                </Button>,
              ]}
            >
              <Typography.Text>
                Вы уверены, что хотите удалить персонажа?
                <br />
              </Typography.Text>
              <FormItem help='Введите в поле слово "удалить", чтобы подтвердить действие.'>
                <Input onBlur={(event) => setDialogValue(event.target.value)} />
              </FormItem>
            </Modal>
          </Col>
        </Row>
      </Card>
    </React.Fragment>
  );
};

const Bottom = () => {
  return (
    <Typography>SocialClub: {store.getState().userSlice.login}</Typography>
  );
};

export default CharacterList;
