import React from "react";
import styles from "./Hud.module.css";
import {
  CircularProgressbarWithChildren,
  buildStyles,
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

import Iphone from "../Iphone/Iphone";
import VehicleHud from "./VehicleHud";

import { Button, Col, Row, Typography } from "antd";

import { Icon } from "@mdi/react";
import {
  mdiFoodSteak,
  mdiCupWater,
  mdiEmoticonExcitedOutline,
  mdiEmoticonNeutralOutline,
  mdiEmoticonSadOutline,
  mdiBankOutline,
  mdiCash100,
  mdiClockOutline,
  mdiCalendarMonthOutline,
  mdiCrosshairsGps,
  mdiMicrophoneOutline,
  mdiMicrophone,
} from "@mdi/js";
import { loadCharacter, setValue } from "./../../store/loadCharacter";
import store, { RootState } from "../../store/index";
import { useSelector } from "react-redux";

const Hud = () => {
  const { accountID } = useSelector((state: RootState) => state.characterSlice);
  return (
    <div className={styles.main}>
      <div className={styles.phone}>
        <Iphone />
      </div>
      {/* <VehicleHud /> */}
      <div className={styles.leftBottom}>
        <LeftBar />
      </div>
      <div className={styles.leftBottomMain}>
        <LeftStatistics />
      </div>
      <LeftBottomNav />
    </div>
  );
};

export const Logo: React.FunctionComponent = () => {
  return (
    <div className={styles.logo}>
      <Typography>SemanticRP</Typography>
      <Typography>ID: 1000 [@100000]</Typography>
    </div>
  );
};

const LeftBar: React.FC = () => {
  return (
    <React.Fragment>
      <LeftBottomBar value={21} type="food" />
      <LeftBottomBar value={50} type="drink" />
      <LeftBottomBar value={100} type="mood" />
    </React.Fragment>
  );
};

const LeftBottomBar: React.FC<{ value: number; type: string }> = (props) => {
  const percentage: number = props.value;
  let IconImage, iconColor;
  switch (props.type) {
    case "food":
      IconImage = mdiFoodSteak;
      break;
    case "drink":
      IconImage = mdiCupWater;
      break;
    case "mood": {
      switch (true) {
        case percentage >= 75:
          IconImage = mdiEmoticonExcitedOutline;
          break;
        case percentage < 75 && percentage > 25:
          IconImage = mdiEmoticonNeutralOutline;
          break;
        case percentage <= 25:
          IconImage = mdiEmoticonSadOutline;
          break;
        default:
          IconImage = mdiEmoticonNeutralOutline;
      }
      break;
    }
    default:
      IconImage = mdiCupWater;
  }
  switch (true) {
    case props.value >= 75:
      iconColor = "#1890ff";
      break;
    case props.value < 75 && props.value > 25:
      iconColor = "#fa8c16";
      break;
    case props.value <= 25:
      iconColor = "#f5222d";
      break;
    default:
      iconColor = "#1890ff";
  }

  return (
    <div className={styles.circularProgress}>
      <CircularProgressbarWithChildren
        value={percentage}
        background
        styles={buildStyles({
          strokeLinecap: "round",
          pathTransitionDuration: 0.5,
          pathColor: "rgba(0, 0, 0, 0.5)",
          trailColor: "transparent",
          backgroundColor: "rgba(0, 0, 0, 0.5)",
        })}
      >
        {props.value <= 25 ? (
          <Icon
            path={IconImage}
            className={styles.circularImageBlink}
            style={{ width: "1vw", marginTop: -5, color: iconColor }}
          />
        ) : (
          <Icon
            path={IconImage}
            style={{ width: "1vw", marginTop: -5, color: iconColor }}
          />
        )}
        <div style={{ fontSize: 10, marginTop: -3, color: "#fff" }}>
          <strong>{percentage}%</strong>
        </div>
      </CircularProgressbarWithChildren>
    </div>
  );
};

const LeftBottomNav = () => {
  return (
    <div className={styles.leftBottomNav}>
      <Row align="middle">
        <Col>
          <Icon
            path={mdiCrosshairsGps}
            size={1}
            style={{ verticalAlign: "middle", color: "#1890ff" }}
          />
        </Col>
        <Col style={{ marginLeft: "0.2vw" }}>
          <Typography style={{ fontWeight: "bold", paddingRight: "0.5vw" }}>
            Paleto Bay
          </Typography>
          <Typography style={{ fontSize: "1vh" }}>ASD</Typography>
        </Col>
        <Col
          style={{
            borderLeft: "1px solid white",
            margin: "0 0.5vw",
            paddingLeft: "0.5vw",
          }}
        >
          <Icon
            path={mdiMicrophoneOutline}
            size={1}
            style={{ verticalAlign: "middle" }}
          />
        </Col>
      </Row>
    </div>
  );
};

const LeftStatistics = () => {
  const { money } = useSelector((state: RootState) => state.characterSlice);
  let formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  });
  return (
    <div className={styles.leftStatistics}>
      <AddItemToBar type="money" body={formatter.format(money)} />
      <AddItemToBar type="bank" body={formatter.format(100000)} />
      <Row align="middle">
        <Col>
          <Row align="middle">
            <Col>
              <Icon
                path={mdiClockOutline}
                size="1.7vh"
                style={{
                  verticalAlign: "middle",
                }}
              />
            </Col>
            <Col>
              <Typography.Text
                style={{
                  fontWeight: "bold",
                  fontSize: "1.7vh",
                }}
              >
                <Time data="time" />
              </Typography.Text>
            </Col>
          </Row>
          <Row align="middle">
            <Col>
              <Icon
                path={mdiCalendarMonthOutline}
                size="1.7vh"
                style={{
                  verticalAlign: "middle",
                }}
              />
            </Col>
            <Col>
              <Typography
                style={{
                  fontWeight: "bold",
                  fontSize: "1.7vh",
                }}
              >
                <Time data="date" />
              </Typography>
              <Typography
                style={{
                  fontWeight: "bold",
                  fontSize: "1vh",
                  textAlign: "end",
                }}
              >
                <Time data="day" />
              </Typography>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

const Time = (props: any) => {
  const [time, setTime] = React.useState(new Date());
  React.useEffect(() => {
    const timer = setInterval(() => {
      setTime(new Date());
    }, 1000);
    return () => {
      clearInterval(timer);
    };
  }, []);
  const clock = time.toLocaleTimeString("ru-RU", {
    hour: "numeric",
    minute: "numeric",
  });
  const date = time.toLocaleDateString("ru-RU");
  const day = new Intl.DateTimeFormat("ru-RU", { weekday: "long" }).format(
    time
  );
  let element: any;
  switch (props.data) {
    case "time":
      element = clock;
      break;
    case "date":
      element = date;
      break;
    case "day":
      element = day.charAt(0).toUpperCase() + day.slice(1);
      break;
    default:
      element = clock;
  }
  return <div>{element}</div>;
};

const AddItemToBar: React.FC<{ type: string; body: string }> = (props) => {
  let IconName;
  switch (props.type) {
    case "money":
      IconName = mdiCash100;
      break;
    case "bank":
      IconName = mdiBankOutline;
      break;
    case "time":
    default:
      IconName = mdiCash100;
  }
  return (
    <Row align="middle">
      <Col>
        <Icon
          path={IconName}
          size="1.7vh"
          style={{
            verticalAlign: "middle",
          }}
        />
      </Col>
      <Col>
        <Typography
          style={{
            fontWeight: "bold",
            fontSize: "1.7vh",
          }}
        >
          {props.body}
        </Typography>
      </Col>
    </Row>
  );
};

export default Hud;
