import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export type CharacterI = {
  accountID: number;
  name: string;
  money: number;
};

export const initialState: CharacterI = {
  accountID: 0,
  name: "Unknown",
  money: 0,
};

const characterSlice = createSlice({
  name: "char",
  initialState,
  reducers: {
    loadCharacter: (state: CharacterI, action: PayloadAction<CharacterI>) => {
      state.accountID = action.payload.accountID;
      state.name = action.payload.name;
      state.money = action.payload.money;
    },
    setValue: (state: any, action: any) => {
      return { ...state, ...action.payload };
    },
  },
});

export const { loadCharacter, setValue } = characterSlice.actions;
(window as any).setValue = setValue;
export default characterSlice.reducer;
