import React, { ReactText } from "react";
import "./Iphone.scss";
import Animate from "rc-animate";
import DTMF from "@kimmel/dtmf";
import styles from "./IphoneCall.module.scss";
import { Col, Row, Typography } from "antd";
import { Icon } from "@iconify/react-with-api";
import useSound from "use-sound";

import dialSfx from "./../../assets/iphone/dial.mp3";

const IphoneCall = (page: any) => {
  const [activeMenu, setActiveMenu] = React.useState<string>("Набор");
  const [phoneNumber, setPhoneNumber] = React.useState<string>("");
  const dialSound = new DTMF();
  const [play, { stop }] = useSound(dialSfx, {
    interrupt: true,
    // @ts-ignore
    loop: true,
  });

  const handleMouseDown = (value: any) => {
    dialSound.play(value);
  };
  const handleMouseUp = () => {
    dialSound.stop();
  };
  const clickedCallback = (value: ReactText) => {
    if (phoneNumber.length >= 8) return 0;
    setPhoneNumber(phoneNumber + value);
  };
  const callButtonClicked = () => {
    if (!phoneNumber) return 0;
    var phone = parseInt(phoneNumber);
    console.log(phone);
    setTimeout(() => {
      setActiveMenu("Вызов");
    }, 500);
    /*play();
    
    setTimeout(() => {
      stop();
    }, 5000);*/

    //alert(phone);
  };
  let body;
  const AddMenu: React.FC<{ name: string; icon: string }> = (props) => {
    return (
      <Col
        span={6}
        className={
          activeMenu === props.name
            ? styles.bottomMenuActive
            : styles.bottomMenu
        }
        onClick={() => setActiveMenu(props.name)}
      >
        <Row align="middle" justify="center">
          <Icon icon={props.icon} height="24px" />
        </Row>
        <Row align="middle" justify="center">
          {props.name}
        </Row>
      </Col>
    );
  };

  const AddButton: React.FC<{ value: number; letters?: string }> = (props) => {
    return (
      <Col
        span={8}
        className={styles.callButtons}
        onClick={() => clickedCallback(props.value)}
        onMouseDown={() => handleMouseDown(props.value)}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
      >
        <Typography.Text style={{ fontSize: "2.5vh" }}>
          {props.value}
        </Typography.Text>
        <br />
        <Typography.Text style={{ fontSize: "1.2vh" }}>
          {props.letters}
        </Typography.Text>
      </Col>
    );
  };

  switch (activeMenu) {
    case "Набор":
      body = (
        <React.Fragment>
          <div className={styles.phoneNumber}>
            <div
              style={{
                fontSize: "3vh",
                minHeight: "1px",
              }}
            >
              <Animate component="" transitionName="fade">
                {phoneNumber ? <div key="1">{phoneNumber}</div> : null}
              </Animate>
            </div>
            <Animate component="" transitionName="fade">
              {phoneNumber ? (
                <div key="1" className={styles.addContact}>
                  Добавить контакт
                </div>
              ) : null}
            </Animate>
          </div>
          <Row style={{ paddingTop: "10px" }}>
            <AddButton value={1} />
            <AddButton value={2} letters="ABC" />
            <AddButton value={3} letters="DEF" />
          </Row>
          <Row>
            <AddButton value={4} letters="GHI" />
            <AddButton value={5} letters="JKL" />
            <AddButton value={6} letters="MNO" />
          </Row>
          <Row>
            <AddButton value={7} letters="PQRS" />
            <AddButton value={8} letters="TUV" />
            <AddButton value={9} letters="WXYZ" />
          </Row>
          <AddButton value={0} letters="+" />
          <Row>
            <div
              className={styles.callButtonAction}
              style={{
                backgroundColor: "#2DD15B",
              }}
              onClick={() => callButtonClicked()}
            >
              <Typography.Text style={{ fontSize: "3em" }}>
                <Icon
                  icon="ion-ios-call"
                  style={{ verticalAlign: "-0.15em" }}
                />
              </Typography.Text>
            </div>
            <Animate component="" transitionName="fade">
              {phoneNumber ? (
                <div className={styles.phoneCallRemoveDigit}>
                  <Icon
                    icon="ion-backspace"
                    color="#343434"
                    height="38px"
                    onMouseDown={() => setPhoneNumber(phoneNumber.slice(0, -1))}
                  />
                </div>
              ) : null}
            </Animate>
          </Row>
        </React.Fragment>
      );
      break;
    case "Вызов":
      body = (
        <IphoneCallEnd
          number={parseInt(phoneNumber)}
          onEnd={() => setActiveMenu("Набор")}
        />
      );
      break;
    default:
      break;
  }

  return (
    <React.Fragment>
      <div className="device-body">{body}</div>
      {activeMenu !== "Вызов" && (
        <div className="device-body-bottom-menu">
          <Row>
            <AddMenu name="Недавние" icon="ion-time" />
            <AddMenu name="Контакты" icon="ion-person-circle-outline" />
            <AddMenu name="Набор" icon="ion-apps" />
          </Row>
        </div>
      )}
    </React.Fragment>
  );
};

const IphoneCallEnd: React.FC<{ number?: number; onEnd: any }> = (props) => {
  return (
    <React.Fragment>
      <div className={styles.phoneNumber} style={{ fontSize: "3vh" }}>
        {props.number}
      </div>
      <div style={{ color: "grey", textAlign: "center" }}>Вызов...</div>
      <div
        className={styles.callButtonAction}
        style={{ backgroundColor: "#c92a2a" }}
        onClick={() => props.onEnd()}
      >
        <Typography.Text style={{ fontSize: "4vh" }}>
          <Icon icon="ion-ios-call" style={{ verticalAlign: "-0.15em" }} />
        </Typography.Text>
      </div>
    </React.Fragment>
  );
};

export default IphoneCall;
