import React from "react";
import { HashRouter as Switch, Route } from "react-router-dom";

import "./App.css";

import Login from "./components/Login/Login";
import CharacterList from "./components/Login/CharacterList";
import SpawnMenu from "./components/Login/SpawnMenu";
import Hud, { Logo } from "./components/Hud/Hud";
import { Link } from "react-router-dom";

const App: React.FC<{}> = () => {
  return (
    <div className="App">
      <Switch>
        {/* <MenuShow /> */}
        <Route path="/login">
          <Login />
        </Route>
        <Route path="/character">
          <CharacterList />
        </Route>
        <Route path="/spawnmenu">
          <SpawnMenu />
        </Route>
        <Route path="/main">
          <Logo />
          <Hud />
        </Route>
      </Switch>
    </div>
  );
};

export default App;
