import React from "react";
import "./Iphone.scss";
import QueueAnim from "rc-queue-anim";
import styles from "./Iphone.module.scss";
import Background from "./../../assets/iphone/bg.jpg";

import Messages from "./../../assets/iphone/message.svg";
import Music from "./../../assets/iphone/apple-music.svg";
import Phone from "./../../assets/iphone/phone.svg";
import Camera from "./../../assets/iphone/camera.svg";
import News from "./../../assets/iphone/news.svg";
import Safari from "./../../assets/iphone/safari.svg";
import Settings from "./../../assets/iphone/settings.svg";
import Wallet from "./../../assets/iphone/wallet.svg";

import { Col, Row, Typography } from "antd";

import { Icon } from "@iconify/react-with-api";
import IphoneCall from "./IphoneCall";

export default function Iphone() {
  const [page, setPage] = React.useState<number>(0);
  return (
    <div>
      <div className="device device-iphone-x">
        <div className="device-frame">
          <div
            className="device-content"
            style={{ backgroundImage: !page ? `url(${Background})` : "none" }}
          >
            <div className="device-top">
              <div className="device-top-left">
                <Typography.Text>KT</Typography.Text>
              </div>
              <div className="device-top-right">
                <Typography.Text
                  style={{ display: "inline", fontSize: "14px" }}
                >
                  LTE
                  <Icon
                    icon="ion-ios-battery-full"
                    color="white"
                    height="24px"
                    style={{ verticalAlign: "-0.500em", marginLeft: "3px" }}
                  />
                </Typography.Text>
              </div>
              <div style={{ clear: "both" }}></div>
              {!page ? (
                <ShowMenu page={(value: any) => setPage(value)} />
              ) : (
                <ShowPage page={page} />
              )}
            </div>
          </div>
        </div>
        <div className="device-btns-home" onClick={() => setPage(0)} />
        <div className="device-stripe" />
        <div className="device-header" />
        <div className="device-sensors" />
        <div className="device-btns-mute" onClick={() => alert("Mute")} />
        <div className="device-btns" />
        <div className="device-power" onClick={() => alert("Power")} />
      </div>
    </div>
  );
}

const ShowMenu = (props: any) => {
  const toParent = (val: any) => {
    props.page(val);
  };

  let pages = [
    { id: 1, image: Phone },
    { id: 2, image: Safari },
    { id: 3, image: Music },
    { id: 4, image: Wallet },

    { id: 5, name: "Сообщения", image: Messages },
    { id: 6, name: "Камера", image: Camera },
    { id: 7, name: "Новости", image: News },
    { id: 8, name: "Настройки", image: Settings },
  ];
  return (
    <React.Fragment>
      <QueueAnim type="scale">
        <div key="1">
          <div className="device-body">
            <Row>
              {pages.map(
                (page) =>
                  page.id >= 5 && (
                    <AddItem
                      key={page.id}
                      id={page.id}
                      name={page.name}
                      image={page.image}
                      page={toParent}
                    />
                  )
              )}
            </Row>
          </div>
          <div className="device-body-bottom">
            <Row>
              {pages.map(
                (page) =>
                  page.id <= 4 && (
                    <AddItem
                      key={page.id}
                      id={page.id}
                      name={page.name}
                      image={page.image}
                      page={toParent}
                    />
                  )
              )}
            </Row>
          </div>
        </div>
      </QueueAnim>
    </React.Fragment>
  );
};

const AddItem: React.FC<{
  id: number;
  name?: string;
  image: string;
  page: any;
}> = (props) => {
  const handleClick = () => {
    props.page(props.id);
  };
  return (
    <React.Fragment>
      <Col span={6} className="menu-div">
        <img
          src={props.image}
          alt="..."
          className="menu-icon"
          onClick={handleClick}
        />
        <br />
        <Typography.Text>{props.name}</Typography.Text>
      </Col>
    </React.Fragment>
  );
};

const ShowPage: React.FC<{ page: number }> = (props) => {
  let body;
  switch (props.page) {
    case 1:
      body = <IphoneCall />;
      break;
    default:
      body = <div>asd</div>;
      break;
  }

  return (
    <React.Fragment>
      <div className="device-body">
        <QueueAnim type="alpha">
          <div key="1">{body}</div>
        </QueueAnim>
      </div>
    </React.Fragment>
  );
};
