import { createSlice, PayloadAction } from "@reduxjs/toolkit";

type UserI = {
  id: number;
  login: string;
  char1: {
    id: number;
    name: string;
    money: number;
  };
  char2: {
    id: number;
    name: string;
    money: number;
  };
  char3: {
    id: number;
    name: string;
    money: number;
  };
};

export const initialState: UserI = {
  id: 0,
  login: "HighRoller-",
  char1: {
    id: 4,
    name: "Johny",
    money: 100,
  },
  char2: {
    id: 2,
    name: "James",
    money: 50,
  },
  char3: {
    id: 0,
    name: "Lebron",
    money: 0,
  },
};

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    loadUser: (state: UserI, action: PayloadAction<UserI>) => {
      state.id = action.payload.id;
      state.login = action.payload.login;
      state.char1 = action.payload.char1;
      state.char2 = action.payload.char2;
      state.char3 = action.payload.char3;
    },
  },
});

export const { loadUser } = userSlice.actions;
export default userSlice.reducer;
