import { Card, Col, Row, Typography } from "antd";
import React from "react";
import styles from "./SpawnMenu.module.css";
import { LockFilled } from "@ant-design/icons";

import lastExitImage from "./../../assets/spawn/work.jpg";
import defaultSpawnImage from "./../../assets/defaultSpawn.png";
import homeImage from "./../../assets/home.jpg";
import workImage from "./../../assets/spawn/work.jpg";

const SpawnMenu = () => {
  return (
    <React.Fragment>
      <Card title="Выберите место спавна" className={styles.main}>
        <Row gutter={[8, 0]}>
          <Col span={6}>
            <SpawnOn image={lastExitImage} type={1} />
          </Col>
          <Col span={6}>
            <SpawnOn image={defaultSpawnImage} type={2} />
          </Col>
          <Col span={6}>
            <SpawnOff image={homeImage} type={3} />
          </Col>
          <Col span={6}>
            <SpawnOff image={workImage} type={4} />
          </Col>
        </Row>
      </Card>
    </React.Fragment>
  );
};

const SpawnOn = (props: any) => {
  let titleText: string;
  switch (props.type) {
    case 1:
      titleText = "Место выхода";
      break;
    case 2:
      titleText = "Стандартный спавн";
      break;
    case 3:
      titleText = "Дом";
      break;
    case 4:
      titleText = "Организация";
      break;
    default:
      titleText = "Error";
  }
  const clicked = () => {
    alert(props.type);
  };
  return (
    <fieldset
      className={styles.fieldsetOn}
      style={{ backgroundImage: "url(" + props.image + ")" }}
      onClick={clicked}
    >
      <legend className={styles.legend}>{titleText}</legend>
      <Typography className={styles.text}>Выбрать</Typography>
    </fieldset>
  );
};

const SpawnOff = (props: any) => {
  let titleText: string;
  switch (props.type) {
    case 1:
      titleText = "Место выхода";
      break;
    case 2:
      titleText = "Стандартный спавн";
      break;
    case 3:
      titleText = "Дом";
      break;
    case 4:
      titleText = "Организация";
      break;
    default:
      titleText = "Error";
  }
  return (
    <fieldset
      className={styles.fieldsetOff}
      style={{ backgroundImage: "url(" + props.image + ")" }}
    >
      <legend className={styles.legend}>{titleText}</legend>
      <LockFilled className={styles.lockedIcon} />
      <Typography>Недоступно</Typography>
    </fieldset>
  );
};

export default SpawnMenu;
