import React from "react";
import styles from "./Login.module.css";

import { Card, Col, Row, Image, Input, Checkbox, Modal, Form } from "antd";
import Button from "antd-button-color";

import ReactLogo from "../../assets/logo512.png";

import { LockOutlined, MailOutlined, UserOutlined } from "@ant-design/icons";

const Login = () => {
  const [showLogin, setShowLogin] = React.useState(true);

  return (
    <React.Fragment>
      <Card className={styles.main}>
        <Row align="middle" gutter={[8, 0]}>
          <Col span={12}>
            <Card>
              <Image src={ReactLogo} preview={false} />
            </Card>
          </Col>
          <Col span={12}>
            {showLogin ? (
              <LoginInput ShowLogin={() => setShowLogin(false)} />
            ) : (
              <RegistrationInput ShowLogin={() => setShowLogin(true)} />
            )}
          </Col>
        </Row>
      </Card>
    </React.Fragment>
  );
};

const LoginInput = (props: any) => {
  const [form] = Form.useForm();
  const onFinish = (values: any) => {
    alert(JSON.stringify(values));
  };

  return (
    <React.Fragment>
      <Card title="Авторизация">
        <Form form={form} onFinish={onFinish} style={{ textAlign: "left" }}>
          <Form.Item name="name" rules={[{ type: "string", required: true }]}>
            <Input size="large" placeholder="Логин" prefix={<UserOutlined />} />
          </Form.Item>
          <Form.Item
            name="password"
            rules={[{ type: "string", required: true }]}
          >
            <Input.Password
              size="large"
              placeholder="Пароль"
              prefix={<LockOutlined />}
            />
          </Form.Item>
          <Row justify="end">
            <Col>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  Вход
                </Button>
              </Form.Item>
            </Col>
            <Col>
              <Button
                type="success"
                style={{ marginLeft: "1vw" }}
                onClick={() => props.ShowLogin(false)}
              >
                Регистрация
              </Button>
            </Col>
          </Row>
        </Form>
      </Card>
    </React.Fragment>
  );
};

const RegistrationInput = (props: any) => {
  const [isModalVisible, setIsModalVisible] = React.useState(false);
  const [form] = Form.useForm();
  const initialValues = {
    name: "",
    email: "",
    password_1: "",
    password_2: "",
    checkbox: false,
  };
  const onFinish = (values: any) => {
    alert(JSON.stringify(values));
  };
  return (
    <React.Fragment>
      <Card title="Регистрация">
        <Form
          form={form}
          initialValues={initialValues}
          onFinish={onFinish}
          preserve={true}
          style={{ textAlign: "left" }}
        >
          <Form.Item
            name="name"
            rules={[
              {
                type: "string",
                required: true,
                min: 4,
                max: 16,
                pattern: new RegExp(/^[a-zA-Z0-9]+$/),
              },
            ]}
          >
            <Input size="large" placeholder="Логин" prefix={<UserOutlined />} />
          </Form.Item>
          <Form.Item name="email" rules={[{ type: "email", required: true }]}>
            <Input size="large" placeholder="Email" prefix={<MailOutlined />} />
          </Form.Item>
          <Form.Item
            name="password_1"
            hasFeedback
            rules={[{ type: "string", required: true, min: 6, max: 16 }]}
          >
            <Input
              size="large"
              placeholder="Пароль"
              prefix={<LockOutlined />}
            />
          </Form.Item>
          <Form.Item
            name="password_2"
            dependencies={["password_1"]}
            hasFeedback
            rules={[
              { required: true },
              ({ getFieldValue }) => ({
                validator(rule, value) {
                  if (!value || getFieldValue("password_1") === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject("Пароли не совпадают.");
                },
              }),
            ]}
          >
            <Input
              size="large"
              placeholder="Подтвердите пароль"
              prefix={<LockOutlined />}
            />
          </Form.Item>
          <Form.Item
            name="checkbox"
            valuePropName="checked"
            hasFeedback
            rules={[
              {
                validator: (_, value) =>
                  value
                    ? Promise.resolve()
                    : Promise.reject(
                        "Вы должны быть согласны с правилами проекта."
                      ),
              },
            ]}
          >
            <Checkbox>
              Я ознакомлен и согласен с
              <span
                style={{ color: "#1890ff" }}
                onClick={() => {
                  setIsModalVisible(true);
                }}
              >
                {" "}
                правилами{" "}
              </span>
              проекта
            </Checkbox>
          </Form.Item>

          <Modal
            title="Правила проекта"
            visible={isModalVisible}
            closable={false}
            destroyOnClose={true}
            footer={[
              <Button
                key="close"
                type="primary"
                onClick={() => setIsModalVisible(false)}
              >
                Закрыть
              </Button>,
            ]}
          >
            <p>Some contents...</p>
          </Modal>

          <Row justify="end">
            <Col>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  Далее
                </Button>
              </Form.Item>
            </Col>
            <Col style={{ marginLeft: "1vw" }}>
              <Button type="default" onClick={() => props.ShowLogin(true)}>
                Назад
              </Button>
            </Col>
          </Row>
        </Form>
      </Card>
    </React.Fragment>
  );
};

export default Login;
